import { _SHLog } from '../utils';
import { World, WorldData } from '../world';
import {
  PluginRegister,
  Register,
  SHAPIWeb,
  ModLoader,
  CreaterRegister,
  SHAPIUnity,
} from '../system';
import {
  Battle,
  BattleData,
  Buff,
  BuffData,
  Entity,
  EntityData,
  Skill,
  SkillData,
} from '../battle';
import { ItemData } from '../world/bag';
import { LocaleData } from '../world/locate';
import { loadElectron } from './electron';
import { LevelData } from '../world/player';
import { loadSetting, WorldSave } from '../world/saves';

// windowwindow,windownodejs,global
let globalThis: any = window;
if (typeof globalThis === 'undefined') {
  globalThis = global;
}

let hasInit = false;

export interface GlobalSetting {
  /** debug, UI() */
  enableDebug: boolean;
  /** cd */
  showCommonCD: boolean;
}

export interface GlobalData {
  item: Register<ItemData>;
  locale: Register<LocaleData>;

  battle: Register<BattleData>;
  entity: Register<EntityData>;
  skill: Register<SkillData>;
  buff: Register<BuffData>;

  level: Register<LevelData>;
  save: Register<WorldData>;
  other: Register<any>;
  VERSION: string;
}

/** , window, ..*/

/** ,creater,, new */
export interface GlobalCreater {
  battle: CreaterRegister<Battle, World>;
  entity: CreaterRegister<Entity, Battle>;
  skill: CreaterRegister<Skill, Entity>;
  buff: CreaterRegister<Buff, Entity>;
}

export async function init() {
  if (hasInit) {
    return;
  }

  const VERSION = 'beta0.7';
  const globalData: GlobalData = {
    battle: new Register(),
    entity: new Register(),
    buff: new Register(),
    skill: new Register(),
    locale: new Register(),
    item: new Register(),
    other: new Register(),
    level: new Register(),
    save: new Register(),
    VERSION: VERSION,
  };
  globalThis.globalData = globalData;

  globalData.save.registe({
    id: 'default',
    time: 0,
    realTimeFormat: '',
    player: {
      id: 'player',
      locale: '',
      talent: 50,
      exp: 0,
      level: 1,
      bag: {},
    },
    realTime: new Date().getTime(),
    config: {
      difficult: 'normal',
    },
    version: VERSION,
    statistics: {},
  });

  globalThis.creater = {
    battle: new CreaterRegister<Battle, World>((id, parent) => {
      return new Battle(parent, globalData.battle.get(id));
    }),
    entity: new CreaterRegister<Entity, Battle>((id, parent) => {
      return new Entity(parent, globalData.entity.get(id));
    }),
    skill: new CreaterRegister<Skill, Entity>((id, parent) => {
      return new Skill(parent, globalData.skill.get(id));
    }),
    buff: new CreaterRegister<Buff, Entity>((id, parent) => {
      return new Buff(parent, globalData.buff.get(id));
    }),
  };

  globalThis.shlog = new _SHLog();
  if (typeof window !== 'undefined') {
    globalThis.shapi = new SHAPIWeb();
  } else {
    globalThis.shapi = new SHAPIUnity();
  }

  globalThis.mod = new ModLoader();

  globalThis.globalSetting = loadSetting();

  // mod
  require('../../extends');

  // mod
  await mod.load('basic');
  // electron(electron)
  loadElectron();

  globalThis.world = new World();

  shlog.info('!');
  hasInit = true;
}
