import { Row, Col, Slider } from 'antd';
import { useState } from 'react';
import { RConfirm } from '../common/RConfirm';

export function RCultivate() {
  const [value, setValue] = useState(30);
  const onChange = (value: any) => {
    setValue(value);
  };
  const player = world.player;
  return (
    <div>
      <p>: {player.levelData.cultivateSpeed}</p>
      <p>: {player.cultivateSpeedByTalent}</p>
      <p>: {player.cultivateSpeed}</p>

      <Row>
        <Col span={12}>
          <Slider
            min={1}
            max={10000}
            onChange={onChange}
            value={typeof value === 'number' ? value : 0}
          />
        </Col>
        <Col span={3}>
          <div>{world.time.parse(value).timStr}</div>
        </Col>
      </Row>
      <RConfirm
        title=""
        msg={`, :${world.time.parse(value).timStr}, :${
          value * player.cultivateSpeed
        }`}
        onClick={() => {
          player.cultivate(value);
        }}
      ></RConfirm>
      {player.canBreakthrough ? (
        <div>
          <p> [{player.nextLevelName}] </p>
          <RConfirm
            title=""
            msg={`${player.nextLevelName}`}
            onClick={() => {
              player.breakthrough();
            }}
          ></RConfirm>
        </div>
      ) : (
        <p> </p>
      )}
    </div>
  );
}
