import { Battle } from '../../core/battle';
import './zjplayer';
import './mai';
import './enemy';

creater.battle.registe('', (world) => {
  const battle = new Battle(world, {
    id: '',
    help: `: 
  1., .
  2..
  3.80%,,.
  4., ,.
  5.,, ,.
  
  
  tips: , .
  `,
    userInterface: '',
    custom: {
      progress: 0,
      progressMax: 500,
    },
  });
  const addSpeed = world.config.difficult === 'test' ? 50 : 5;

  for (let i = 0; i < 8; i++) {
    battle.createEntity('mai', 'left');
  }

  // 
  battle.createEntity('zjEnemy', 'right');
  // 
  battle._player = battle.createEntity('zjPlayerV2', 'left');
  // , 
  battle.lefts.pop();

  battle.eventPerSecond.on(() => {
    battle.custom.progress += addSpeed;
  });

  battle.eventUpdate.on(() => {
    if (battle.custom.progress >= battle.custom.progressMax) {
      shlog.info(`, !`);
      battle.state.emit('success');
    }
  });

  return battle;
});
