import {
  Battle,
  Entity,
  EntityController,
  EntityData,
} from '../../core/battle';

creater.battle.registe('', (world) => {
  const battle = new Battle(world, {
    id: '',
    help: `:
  !`,
    player: 'yyPlayer',
    lefts: ['yyPlayer'],
    rights: [''],
  });
  return battle;
});

creater.entity.registe('yyPlayer', (battle) => {
  const data: EntityData = {
    id: 'yyPlayer',
    skills: [],
    hp: 100,
    hpmax: 100,
  };
  const entity = new Entity(battle, data);
  entity.playerController = new EntityController(entity);
  return entity;
});

creater.entity.registe('', (battle) => {
  const data: EntityData = {
    id: '',
    skills: [],
    hp: 10000,
    hpmax: 10000,
  };
  return new Entity(battle, data);
});
