import { Battle, Buff, Entity, Skill } from '../../core/battle';
import { fisherYates, randomInt } from '../../core/utils';

creater.entity.registe('zjEnemy', (battle: Battle) => {
  const entity = new Entity(battle, {
    id: 'zjEnemy',
    skills: ['', ''],
  });

  let hasSkill3 = false;
  entity.eventUpdate.on(() => {
    if (battle.state.value !== 'running') {
      return;
    }
    // 50%
    let p = world.config.difficult === 'test' ? 0.1 : 0.5;
    if (battle.custom.progress >= battle.custom.progressMax * p) {
      if (!hasSkill3) {
        entity.createSkill('');
        hasSkill3 = true;
      }
    }

    // 
    Object.values(entity.skills).forEach((skill) => {
      if (skill.cd > 0) {
        skill.cdRelease -= entity.battle.deltaTime;
        if (skill.cdRelease < 0) {
          skill.eventUse.emit();
        }
      }
    });
  });

  return entity;
});

creater.skill.registe('', (entity) => {
  const skill = new Skill(entity, {
    id: '',
    name: '',
    type: 'effect',
    desc: '10, 1s,2%',
    cd: 1,
  });
  skill.eventUse.on(() => {
    const dmg = 10 * (skill.entity.battle.battleStartTime * 0.02 + 1);
    skill.custom.dmg = dmg;
    const jingmais = skill.entity.getEnemys();
    if (!jingmais.length) {
      return;
    }
    // 5,
    for (let i = 0; i < dmg; i++) {
      jingmais[randomInt(0, jingmais.length - 1)].hit(1);
    }
  });
  return skill;
});

creater.buff.registe('', (entity) => {
  return new Buff(entity, {
    id: '',
    type: 'negative',
    desc: `5`,
    release: 5,
  });
});

creater.skill.registe('', (entity) => {
  const skill = new Skill(entity, {
    id: '',
    type: 'effect',
    desc: ', 6s 60 ',
    cd: 20,
    cdRelease: 14,
  });

  let dmg = 60;
  const hitAfter = (target: Entity) => {
    const buff = target.createBuff('');
    buff.eventRemove.on(() => {
      target.hit(dmg);
    });
  };

  skill.eventUse.on(() => {
    const jingmais = skill.entity.getEnemys();
    if (!jingmais.length) {
      return;
    }
    if (jingmais.length <= 3) {
      jingmais.forEach((jm) => {
        hitAfter(jm);
      });
    } else {
      fisherYates(jingmais);
      for (let i = 0; i < 3; i++) {
        hitAfter(jingmais[i]);
      }
    }
  });
  return skill;
});

creater.buff.registe('', (entity) => {
  return new Buff(entity, {
    id: '',
    type: 'negative',
    desc: `5`,
    release: 5,
  });
});

creater.skill.registe('', (entity) => {
  const skill = new Skill(entity, {
    id: '',
    type: 'effect',
    desc: 'hp, 6s 100 ',
    cd: 8,
    cdRelease: 6,
  });

  let dmg = 60;
  const hitAfter = (target: Entity) => {
    const buff = target.createBuff('');
    target.inDanger = true;
    buff.eventRemove.on(() => {
      target.hit(dmg);
      target.inDanger = false;
    });
  };

  skill.eventUse.on(() => {
    const jingmais = skill.entity.getEnemys();
    if (!jingmais.length) {
      return;
    }

    const least = jingmais.sort((a, b) => {
      return a.hpbar.value - b.hpbar.value;
    })[0];

    hitAfter(least);
  });

  return skill;
});
