import { Entity } from '.';
import { CommonData, CommonObject, PluginRegister } from '../system';
import { SHEvent, EventCanUse } from '../utils';

export interface SkillData extends CommonData {
  /**
   * cast , (),cd, 
   * instance ,cd(),
   * duration , , ().
   * effet 
   */
  type: 'cast' | 'instance' | 'duration' | 'effect';

  castCost?: number;
  /** type=duration, */
  effectInterval?: number;
  /** , -1, 0 */
  useLimit?: number;

  cd?: number;
  cdRelease?: number;

  icon?: string;
}

export class Skill extends CommonObject<SkillData> {
  type: 'cast' | 'instance' | 'duration' | 'effect';

  /** ,0 */
  castCost: number;
  /**  -1 */
  useLimit: number;
  /**  */
  cd: number;
  /**  */
  cdRelease: number;
  /**  */
  icon: string;
  /**  */
  entity: Entity;

  /**   */
  target: Entity;

  eventUse: SHEvent = new SHEvent();
  eventCanUse: EventCanUse = new EventCanUse();
  /** , , .. */
  eventDuration: SHEvent = new SHEvent();
  /**  */
  eventCancel: SHEvent = new SHEvent();

  custom: any = {};

  /** UI */
  public get iconData() {
    let desc = this.name;
    let percent = 100;
    let center = undefined;
    if (this.cd > 0) {
      if (this.cdRelease <= 0) {
        percent = 100;
      } else {
        center = this.cdRelease.toFixed(1) + 's';
        percent = ((this.cd - this.cdRelease) / this.cd) * 100;
      }
    }

    const control = this.entity.playerController;
    if (control) {
      if (control.commonCDRelease > 0 && percent === 100) {
        percent = (1 - control.commonCDRelease / control.commonCD) * 100;
      }
    }

    if (this.useLimit === 0) {
      percent = 0;
    } else if (this.useLimit > 0) {
      center = this.useLimit.toString();
    }

    return { desc, percent, center };
  }

  constructor(entity: Entity, data: SkillData) {
    super(data);
    this.entity = entity;

    this.type = data.type;
    this.castCost = data.castCost || 0;
    this.useLimit = data.useLimit || -1;
    this.cd = data.cd || 0;
    this.cdRelease = data.cdRelease || 0;
    this.icon = data.icon || 'default';
    /**  */
    this.target = entity;

    this.eventCanUse.on(() => {
      if (this.cdRelease > 0) {
        shlog.warn(` [${this.id}] `);
        return false;
      }
      if (this.useLimit === 0) {
        shlog.warnInGame(` [${this.id}] `);
        return false;
      }
      return true;
    });

    this.eventUse.on(() => {
      this.cdRelease = this.cd;
      if (this.useLimit >= 0) {
        this.useLimit--;
      }
    });
  }
}
