import { CommonObject, CommonData } from './commonData';

export class CreaterRegister<TObj extends CommonObject<CommonData>, TParent> {
  _creater: { [key: string]: any } = {};

  _defaultCreater: (id: string, parent: TParent) => TObj;
  constructor(defaultCreater: (id: string, parent: TParent) => TObj) {
    this._defaultCreater = defaultCreater;
  }

  /** json,,ts */
  registe(key: string, callback: (parent: any) => TObj) {
    this._creater[key] = callback;
  }

  create(id: string, parent: TParent, ...args: any) {
    // creater,creater,
    if (this._creater[id]) {
      return this._creater[id](parent, ...args);
    } else {
      // idcreater
      return this._defaultCreater(id, parent);
    }
  }
}

export class Register<T extends { id: string }> {
  _data: { [key: string]: T } = {};

  registeGroup(data: Array<T>) {
    data.forEach((d) => {
      this.registe(d);
    });
  }
  registe(data: T) {
    if (this._data[data.id]) {
      shlog.info(`key,:${data.id}`);
    }
    this._data[data.id] = data;
  }

  get(id: string | number) {
    const res = this._data[String(id)];
    if (!res) {
      shlog.error(`id:${id}, key:`, this._data);
    }
    return res;
  }
}

export class PluginRegister<T extends CommonObject<CommonData>> {
  data: { [key: string]: any } = {};
  /** json,,ts */
  registe(key: string, callback: (e: T, ...args: any) => void) {
    this.data[key] = callback;
  }
  call(e: any, key: string, ...args: any) {
    this.data[key](e, ...args);
  }
}
