import { randomInt } from '.';

const names =
  '                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  ';

const nameArray = names.split(' ');

export function getName() {
  return nameArray[randomInt(nameArray.length)];
}
