import { EntityData, Entity } from '../../core/battle';

globalData.buff.registe({
  id: '',
  type: 'positive',
  desc: ', 1/s',
});

const mais_id = [
  '',
  '',
  '',
  '',
  '',
  '',
  '',
  '',
];

let index = 0;
creater.entity.registe('mai', (battle) => {
  const data: EntityData = {
    id: mais_id[index],
    hp: 100,
    hpmax: 300,
    skills: [],
  };
  index++;

  const e = new Entity(battle, data);

  battle.eventPerSecond.on(() => {
    if (e.hpbar.value >= e.hpbar.maxValue * 0.8) {
      battle.progress += 1;
      e.createBuff('');
    } else {
      e.removeBuff('');
    }

    if (e.buffs['zjPlayerV2_']) {
      let dmg = e.buffs['zjPlayerV2_'].stack * 1;
      e.hit(dmg);
    }
  });

  e.eventDead.on(() => {
    shlog.warn(``);
    battle.state.emit('fail');
  });

  return e;
});
