import { SHEvent } from '../utils';

export class InputManager {
  /** ,  */
  skillFirstLine = [
    'skill1_1',
    'skill1_2',
    'skill1_3',
    'skill1_4',
    'skill1_5',
    'skill1_6',
  ];

  /** ,  */
  skillSecondLine = [
    'skill2_1',
    'skill2_2',
    'skill2_3',
    'skill2_4',
    'skill2_5',
    'skill2_6',
  ];

  /** keycode  */
  keyboard_key: { [key: string]: string } = {
    '1': 'skill1_1',
    '2': 'skill1_2',
    '3': 'skill1_3',
    '4': 'skill1_4',
    '5': 'skill1_5',
    '6': 'skill1_6',
    q: 'skill2_1',
    w: 'skill2_2',
    e: 'skill2_3',
    r: 'skill2_4',
    t: 'skill2_5',
    y: 'skill2_6',
    ' ': 'interrupt',
  };

  /**  keycode, map */
  key_keyboard: { [key: string]: string } = {};

  eventKey: SHEvent<string> = new SHEvent();
  constructor() {
    Object.keys(this.keyboard_key).forEach((key) => {
      const value = this.keyboard_key[key];
      this.key_keyboard[value] = key;
    });

    shapi.eventKeyUp((key: string) => {
      if (this.keyboard_key[key]) {
        this.eventKey.emit(this.keyboard_key[key]);
        shlog.info(`:[${key}]-[${this.keyboard_key[key]}]`);
      } else {
        shlog.warn(`:[${key}]-[]`, key);
      }
    });
  }
}
