import { PluginRegister } from '.';

export interface CommonData {
  id: string;
  name?: string;
  desc?: string;
  custom?: any;
}

export abstract class CommonObject<T extends CommonData> {
  id: string;
  _name?: string;
  /**  */
  desc: string;
  data: T;
  custom?: any;
  public get name() {
    return this._name ? this._name : this.id;
  }

  /** load */
  constructor(data: T) {
    this.id = data.id;
    this._name = data.name;
    this.data = data;
    this.desc = data.desc || '';
    this.custom = data.custom;
  }

  /** save,  */
  save(): T {
    throw new Error('save()');
  }
}
