/* eslint-disable no-eval */
export class ModLoader {
  async load(id: string) {
    const uri = `mods/${id}/index.js`;

    /** mod, , mod */
    await shapi
      .loadFileText(uri)
      .then((text) => {
        shlog.log(
          `mod:${id}`
          // text.length > 100 ? text.slice(0, 100) + '...' : text
        );
        try {
          eval(text);
        } catch (e) {
          shlog.errorInGame(`mod:${id} ,js`, e);
        }
      })
      .catch((err) =>
        shlog.errorInGame(`mod:${id} , Request Failed`, err)
      );
  }
}
